.toggle {
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 42px;
  height: 34px;

  &__line {
    margin: 0.5rem 0;
    display: block;
    width: 32px;
    height: 1px;

    background-color: $color-default-black;
    transform-origin: center;
    transition: transform $trans-default;
  }
}
